import React, { Component } from "react"
import styled from "styled-components"
import VisibilitySensor from "react-visibility-sensor"
import Button from "../components/button"
import media from "../styles/mediaQueries"
import { white } from "../styles/colors"
import Modernizr from "../utils/modernizr"

const Overlay = styled.section`
  height: 100%;
  width: 100%;
  ${media.smallTablet`max-width: 27rem;`}

  padding: 0 1.2rem;
  ${media.smallTablet`padding: 0 3rem;`}

  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;
`

const SectionWithBackgroundImage = styled.div`
  height: 100vh;
  width: 100vw;
  background-size: cover;
  background-position: 50%;
  ${props => props.background && `background: ${props.background};`}
`

const Title = styled.h2`
  color: rgb(${white});
  font-size: 2.4rem;
  ${media.smallTablet`font-size: 3rem;`}
  font-weight: 800;
  letter-spacing: -2px;
`

const ButtonWrapper = styled.div`
  margin-top: 1rem;
`

class HomeSection extends Component {
  constructor() {
    super();

    this.state = {
      showBackgroundImage: false
    };
  }

  handleLoadBackgroundImage = (visible) => {
    console.log(`${this.props.title} ${visible ? "visible" : "hidden"}`)

    if (visible) {
      this.setState({
        showBackgroundImage: true
      })
    }
  }

  render() {
    const { backgroundImage, buttonColor, buttonHref, buttonText, overlayColor, title } = this.props;
    const { showBackgroundImage } = this.state;

    return (
      <SectionWithBackgroundImage
        background={showBackgroundImage ? `url(${backgroundImage})` : `rgb(${buttonColor})`}
      >
        <Overlay style={{backgroundColor: `rgba(${overlayColor}, .57)`}}>
          <VisibilitySensor
            onChange={this.handleLoadBackgroundImage}
          >
            <Title dangerouslySetInnerHTML={{__html: title}}/>
          </VisibilitySensor>
          <ButtonWrapper>
            <Button color={buttonColor} href={buttonHref}>{buttonText}</Button>
          </ButtonWrapper>
        </Overlay>
      </SectionWithBackgroundImage>
    )
  }
}

export default HomeSection

